.App {
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
    background: #1b3d8d;
}

.navbar__item {
    padding: 16px 16px;
    cursor: pointer;
    vertical-align: middle;
}

h1 {
    font-size: 18px;
    margin-left: 5px;
}

p {
    margin-left: 5px;
    margin-top: 0px;
}

select, input {
  font-size: 1.0rem;
}

.inputFieldContainer {
    margin: 10px 10px 10px 10px;
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.quickHelp {
    margin: 5px 5px 5px 5px;
}

.helpText {
    margin-left: 10px;
    margin-right: 10px;
}

.journey {
    padding: 10px;
    border-top: 1px solid black;
    margin-bottom: 10px;
}

.journeyHeader {
    padding-bottom: 5px;
}

.journeyHeaderDetail {
    font-style: italic;
}

.station {
    padding-left: 10px;
    background-color: LightGray;
    font-weight: bold;
}

.routeOptionBox {
    margin: 5px 5px 5px 5px;
}

.routeOption {
    display: flex;
    flex-direction: row;
    background-color: GhostWhite;
}

.lineNameContainer {
    flex-shrink: 0;
    min-width: 6rem;
}

.lineName {
    font-weight: bold;
    text-align: center;
}

.routeOptionEndpoints {
    display: flex;
    flex-direction: column;
    padding-left: 10px;
    font-style: italic;
}

